import axios from "../../config/axios"
import { getUserToken } from "../functions/auth"
import {
  calculaAutoridadeUsuario,
  autoridadeParaCampos,
} from "../functions/usuario"
import isGraphQLError from "../functions/isGraphQLError"

export const fetchAll = async (dados) => {
  const graphqlQuery = `
		{
			usuarios(id_cliente: ${dados.id_cliente}){
				id
				usr_login
        autoridade
        usuario_inicial
				status
			}
		}
	`
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post("/graphql", { query: graphqlQuery }, request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return res.data.data.usuarios
  } catch (err) {
    throw err
  }
}

export const fetchOne = async (dados) => {
  const graphqlQuery = `
		{ 
			usuario(id_usuario: ${dados.id_usuario}, id_cliente: ${dados.id_cliente}) {
				id
				usr_login
				autoridade
        status
      }
		}
		`
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post("/graphql", { query: graphqlQuery }, request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    const autoridades = autoridadeParaCampos(res.data.data.usuario.autoridade)

    const user = {
      ...res.data.data.usuario,
      ...autoridades,
    }

    return user
  } catch (err) {
    throw err
  }
}

export const create = async (dados) => {
  const autoridade = calculaAutoridadeUsuario(dados.usuario)

  try {
    const graphqlQuery = `
			mutation {
				cadUsuario(
					data: {
						usr_login: "${dados.usuario.usr_login}"
						usr_pswd: "${dados.usuario.usr_pswd ? dados.usuario.usr_pswd : ""}"
						autoridade: "${autoridade}"
						status: ${dados.usuario.status}
					},
					id_cliente: ${dados.id_cliente} 
				)
			}
			`
    const request = {
      headers: {
        Authorization: "Bearer " + getUserToken(),
        "Content-Type": "application/json",
      },
    }

    const res = await axios.post("/graphql", { query: graphqlQuery }, request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return true
  } catch (err) {
    throw err
  }
}

