import React, { useState, useEffect } from "react"
import { Route, useHistory } from "react-router-dom"

import { formatFormData } from "../../forms/functions/form"
import * as usuarioAPI from "../../services/functions/usuario"
import * as clienteAPI from "../../services/functions/cliente"
import idToName from "../../functions/idToName"
import ErrorBoundary from "../../components/ErrorBoundary"
import Datatable from "../../components/Datatable"
import ModalForm from "../../components/ModalForm"
import ConfirmModal from "../../components/UI/ConfirmModal"
import {
  initialValues,
  validationSchema,
  camposForm,
} from "../../forms/forms/usuario"
import {
  initialValues as initialValuesEdit,
  validationSchema as validationSchemaEdit,
  camposForm as camposFormEdit,
} from "../../forms/forms/usuarioEdit"
import { Edit, AddCircle, Delete } from "@material-ui/icons"
import estilos from "./estilos.module.css"

const Usuarios = (props) => {
  const { appState } = props

  const [usuarios, setUsuarios] = useState([])
  const [usuariosA, setUsuariosA] = useState([])
  const [usuario, setUsuario] = useState(null)
  const [clientes, setClientes] = useState([])
  const [linhaSelecionadaClientes, setLinhaSelecionadaClientes] = useState(null)
  const [linhaSelecionadaUsuarios, setLinhaSelecionadaUsuarios] = useState(null)

  const history = useHistory()
  const location = window.location.pathname.split("/")

  const colunasTabelaClientes = [
    {
      id: "codigo",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Código",
    },
    {
      id: "nome",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Nome",
    },
    {
      id: "email_login",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Email",
    },
  ]

  const colunasTabelaUsuarios = [
    {
      id: "usr_login",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Usuário",
    },
    {
      id: "autoridade",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Autoridade",
    },
    {
      id: "usuario_inicial",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Usuário Inicial",
    },
    {
      id: "status",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Ativo",
    },
  ]

  const opcoesTabela = [
    {
      label: "Novo",
      click: () => {
        history.push("/usuarios/novo")
      },
      icon: <AddCircle />,
    },
  ]

  const opcoesTabelaSelecionada = [
    {
      label: "Editar",
      click: () => {
        fetchUsuarioHandler()
        history.push("/usuarios/editar")
      },
      icon: <Edit />,
    },
    {
      label: "Excluir",
      click: () => {
        history.push("/usuarios/excluir")
      },
      icon: <Delete />,
    },
  ]

  const idToNameUsuarios = () => {
    let usrs = idToName(
      [usuarios, "status"],
      [
        [
          { id: 1, nome: "Sim" },
          { id: 0, nome: "Não" },
        ],
        "nome",
      ]
    )

    usrs = idToName(
      [usrs, "usuario_inicial"],
      [
        [
          { id: 1, nome: "Sim" },
          { id: 0, nome: "Não" },
        ],
        "nome",
      ]
    )

    setUsuariosA(usrs)
  }

  const successHandler = () => {
    setLinhaSelecionadaUsuarios(null)
    history.goBack()
    fetchUsuariosHandler()
    appState.setSuccess()
  }

  const clearUsuariosHandler = async () => {
    setUsuarios([])
    setLinhaSelecionadaUsuarios(null)
  }

  const fetchClientesHandler = async () => {
    const res = await clienteAPI.fetchAll({ appState })

    setClientes(res)
  }

  const fetchUsuariosHandler = async () => {
    const res = await usuarioAPI.fetchAll({
      id_cliente: linhaSelecionadaClientes,
      appState,
    })

    setUsuarios(res)
  }

  const fetchUsuarioHandler = async () => {
    setUsuario(null)
    const res = await usuarioAPI.fetchOne({
      id_cliente: linhaSelecionadaClientes,
      id_usuario: linhaSelecionadaUsuarios,
      appState,
    })

    setUsuario(res)
  }

  const cadUsuarioHandler = async (values) => {
    const res = await usuarioAPI.create({
      usuario: values,
      id_cliente: linhaSelecionadaClientes,
      appState,
    })

    if (res) {
      successHandler()
    }
  }

  const editUsuarioHandler = async (values) => {
    const res = await usuarioAPI.update({
      id_cliente: linhaSelecionadaClientes,
      id_usuario: linhaSelecionadaUsuarios,
      usuario: values,
      appState,
    })

    if (res) {
      successHandler()
    }
  }

  const delUsuarioHandler = async () => {
    const res = await usuarioAPI.del({
      id_cliente: linhaSelecionadaClientes,
      id_usuario: linhaSelecionadaUsuarios,
      appState,
    })

    if (res) {
      successHandler()
    }
  }

  const onFormSubmit = async (values) => {
    let newValues = await formatFormData(values)

    if (location[2] === "novo") {
      cadUsuarioHandler(newValues)
    } else if (location[2] === "editar") {
      editUsuarioHandler(newValues)
    }
  }

  useEffect(() => {
    fetchClientesHandler()
  }, [])

  useEffect(() => {
    if (!!linhaSelecionadaClientes) {
      clearUsuariosHandler()
      fetchUsuariosHandler()
    } else {
      clearUsuariosHandler()
    }
  }, [linhaSelecionadaClientes])

  useEffect(() => {
    idToNameUsuarios()
  }, [clientes, usuarios])

  return (
    <ErrorBoundary>
      <div className={estilos.Container}>
        <div className={estilos.Tabelas}>
          <Datatable
            caixaPesquisa={true}
            colunasKey={"id"}
            colunasTabela={colunasTabelaClientes}
            fetchedRows={clientes}
            linhasPorPaginaSelecionada={5}
            linhaSelecionada={linhaSelecionadaClientes}
            loading={appState.loading}
            opcoesTabela={[]}
            opcoesTabelaSelecionada={[]}
            setLinhaSelecionada={setLinhaSelecionadaClientes}
            tituloTabela={"Clientes"}
          />

          <Datatable
            caixaPesquisa={false}
            colunasKey={"id"}
            colunasTabela={colunasTabelaUsuarios}
            fetchedRows={usuariosA}
            linhasPorPaginaSelecionada={5}
            linhaSelecionada={linhaSelecionadaUsuarios}
            loading={appState.loading}
            opcoesTabela={linhaSelecionadaClientes ? opcoesTabela : []}
            opcoesTabelaSelecionada={opcoesTabelaSelecionada}
            setLinhaSelecionada={setLinhaSelecionadaUsuarios}
            tituloTabela={"Usuários"}
          />
        </div>
      </div>

      <Route path="/usuarios/excluir">
        <ConfirmModal
          action={"delete"}
          history={history}
          location={location}
          okButtonClick={delUsuarioHandler}
        />
      </Route>

      <Route path="/usuarios/novo">
        <ModalForm
          camposForm={camposForm({ loading: appState.loading })}
          initialValues={initialValues}
          history={history}
          loading={appState.loading}
          location={location}
          onFormSubmit={onFormSubmit}
          tabLabel={[{ label: "Usuario" }]}
          validationSchema={validationSchema}
        />
      </Route>

      <Route path="/usuarios/editar">
        {linhaSelecionadaUsuarios ? (
          <ModalForm
            camposForm={camposFormEdit({ loading: appState.loading })}
            initialValues={usuario ? usuario : initialValuesEdit}
            history={history}
            loading={appState.loading}
            location={location}
            onFormSubmit={onFormSubmit}
            tabLabel={[{ label: "Usuario" }]}
            validationSchema={validationSchemaEdit}
          />
        ) : null}
      </Route>
    </ErrorBoundary>
  )
}

export default Usuarios
