const idToName = ([list1, id1], [list2, id2]) => {
	if (!list1) {
		return
	}
	if (!list2) {
		return
	}

	const newList = list1.map((item) => {
		const id = item[id1]

		const op = list2.find((i) => i.id === id)

		if (!op) {
			return item
		}

		return {
			...item,
			[id1]: op[id2],
		}
	})

	return newList
}

export default idToName
