import * as usuarioAPI from "../api/usuario"

export const calculaAutoridadeUsuario = (dados) => {
  const {
    administrador_geral,
    permitir_acesso_caixa,
    permitir_fechamento_mes,
    permitir_reabertura_mes,
  } = dados

  if (administrador_geral) {
    return 0
  }

  if (
    permitir_acesso_caixa &&
    permitir_fechamento_mes &&
    permitir_reabertura_mes
  ) {
    return 8
  }
  if (permitir_fechamento_mes && permitir_reabertura_mes) {
    return 7
  }

  if (permitir_acesso_caixa && permitir_reabertura_mes) {
    return 6
  }

  if (permitir_acesso_caixa && permitir_fechamento_mes) {
    return 4
  }

  if (permitir_reabertura_mes) {
    return 5
  }

  if (permitir_fechamento_mes) {
    return 3
  }

  if (permitir_acesso_caixa) {
    return 2
  }

  return 1
}

export const autoridadeParaCampos = (autoridade) => {
  if (Number.parseInt(autoridade) === 0) {
    return {
      administrador_geral: true,
      permitir_acesso_caixa: false,
      permitir_fechamento_mes: false,
      permitir_reabertura_mes: false,
    }
  }

  if (Number.parseInt(autoridade) === 8) {
    return {
      administrador_geral: false,
      permitir_acesso_caixa: false,
      permitir_fechamento_mes: false,
      permitir_reabertura_mes: false,
    }
  }

  if (Number.parseInt(autoridade) === 7) {
    return {
      administrador_geral: false,
      permitir_acesso_caixa: false,
      permitir_fechamento_mes: true,
      permitir_reabertura_mes: true,
    }
  }

  if (Number.parseInt(autoridade) === 6) {
    return {
      administrador_geral: false,
      permitir_acesso_caixa: true,
      permitir_fechamento_mes: false,
      permitir_reabertura_mes: true,
    }
  }

  if (Number.parseInt(autoridade) === 4) {
    return {
      administrador_geral: false,
      permitir_acesso_caixa: true,
      permitir_fechamento_mes: true,
      permitir_reabertura_mes: false,
    }
  }

  if (Number.parseInt(autoridade) === 5) {
    return {
      administrador_geral: false,
      permitir_acesso_caixa: false,
      permitir_fechamento_mes: false,
      permitir_reabertura_mes: true,
    }
  }

  if (Number.parseInt(autoridade) === 3) {
    return {
      administrador_geral: false,
      permitir_acesso_caixa: false,
      permitir_fechamento_mes: true,
      permitir_reabertura_mes: false,
    }
  }

  if (Number.parseInt(autoridade) === 2) {
    return {
      administrador_geral: false,
      permitir_acesso_caixa: true,
      permitir_fechamento_mes: false,
      permitir_reabertura_mes: false,
    }
  }

  return {
    administrador_geral: false,
    permitir_acesso_caixa: false,
    permitir_fechamento_mes: false,
    permitir_reabertura_mes: false,
  }
}

export const fetchAll = async (actions) => {
  actions.appState.startLoading()

  try {
    const res = await usuarioAPI.fetchAll({
      id_cliente: actions.id_cliente,
    })

    actions.appState.stopLoading()

    return res
  } catch (err) {
    actions.appState.stopLoading()
    actions.appState.setError(err)
  }

  return
}

