import * as Yup from "yup"
import translate from "../../config/yup"

export const initialValues = {
	usr_login: "",
	usr_pswd: "",
	status: true,
	administrador_geral: false,
	permitir_acesso_caixa: false,
	permitir_fechamento_mes: false,
	permitir_reabertura_mes: false,
}

Yup.setLocale(translate)
export const validationSchema = () => {
	return Yup.object().shape({
		usr_login: Yup.string().min(6).max(32).required(),
		usr_pswd: Yup.string().min(8).max(32).required(),
	})
}

export const camposForm = (props) => {
	return [
		[
			{
				config: { xs: 12, sm: 6, xl: 6 },
				type: "column",
				fields: [
					{ type: "label", family: "component", label: "Informações de login" },
					{
						family: "input",
						type: "text",
						label: "Usuário",
						name: "usr_login",
						config: {
							disabled: props.loading,
							max: 32,
						},
					},
					{
						family: "input",
						type: "password",
						label: "Senha",
						name: "usr_pswd",
						config: {
							disabled: props.loading,
							max: 32,
						},
					},
					{
						family: "input",
						type: "checkbox",
						label: "Ativo",
						name: "status",
						config: {
							disabled: props.loading,
							max: 30,
						},
					},
				],
			},
			{
				config: { xs: 12, sm: 6, xl: 6 },
				type: "column",
				fields: [
					{
						family: "rule",
						type: "rule",
						field: "administrador_geral",
						becomes: true,
						set: "permitir_acesso_caixa",
						to: true,
					},
					{
						family: "rule",
						type: "rule",
						field: "administrador_geral",
						becomes: true,
						set: "permitir_fechamento_mes",
						to: true,
					},
					{
						family: "rule",
						type: "rule",
						field: "administrador_geral",
						becomes: true,
						set: "permitir_reabertura_mes",
						to: true,
					},

					{
						family: "rule",
						type: "rule",
						field: "administrador_geral",
						becomes: false,
						set: "permitir_acesso_caixa",
						to: false,
					},
					{
						family: "rule",
						type: "rule",
						field: "administrador_geral",
						becomes: false,
						set: "permitir_fechamento_mes",
						to: false,
					},
					{
						family: "rule",
						type: "rule",
						field: "administrador_geral",
						becomes: false,
						set: "permitir_reabertura_mes",
						to: false,
					},

					{ type: "label", family: "component", label: "Permissões" },
					{
						family: "input",
						type: "checkbox",
						label: "Administrador Geral",
						name: "administrador_geral",
						config: {
							disabled: props.loading,
						},
					},
					{
						family: "input",
						type: "checkbox",
						label: "Permitir Acesso ao Caixa",
						name: "permitir_acesso_caixa",
						config: {
							disabled: props.loading,
						},
					},
					{
						family: "input",
						type: "checkbox",
						label: "Permitir Fechamento de Mês",
						name: "permitir_fechamento_mes",
						config: {
							disabled: props.loading,
						},
					},
					{
						family: "input",
						type: "checkbox",
						label: "Permitir Reabertura de Mês",
						name: "permitir_reabertura_mes",
						config: {
							disabled: props.loading,
						},
					},
				],
			},
		],
	]
}
